@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700;800;900&display=swap);
#header {
    background-color: rgb(29, 29, 58);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 12px;
    position: fixed;
    width: 100%;
    z-index: 10;
    height: 80px;
}

#header #pageName {
    display: flex;
    flex-direction: row;
    align-items: center;
    /* background-color: #555555; */
    padding: 0 8px;
    cursor: pointer;
}

#header .utilitiesNav,
#header .sectionNav {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 12px;
    padding-right: 0px;
    /* background-color: beige; */
}

#header .navItem {
    margin: 0 8px;
    cursor: pointer;
    list-style-type: none;
    transition: 250ms;
}

#header .navItem > a {
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
    transition: 300ms;
}

#header .navItem > a:hover{
    color: rgb(132, 132, 221); 
    font-size: 28px;
    padding: 0 16px;
}

#header .utilitiesNav .navItem button {
    background-color: #36326b;
    border: none;
    cursor: pointer;
    padding: 8px 16px;
    transition: 250ms;
    border-radius: 4px;
}

#header .utilitiesNav .navItem button:hover {
    color: rgb(11, 11, 104);
    background-color: rgb(201, 200, 252);
}
#Profile {
    padding-top: 120px;
    display: flex;
    flex-direction: column;
}



#basicInfo {
    
}


#basicInfo .imgContainer {
    display: inline-block;
    min-width: 320px;
    width: 320px;
    height: 320px;
    border-radius: 320px;
    background-color: #000;
    overflow: hidden;
}

#basicInfo .imgContainer img {
    height: 440px;
    width: 440px;
    -webkit-transform: translateX(-12.5%) translateY(-15%);
            transform: translateX(-12.5%) translateY(-15%);
}

#basicInfo .txtContainer {
    display: inline-block;
    background-color: #c78aad00;
    width: 75%;
    padding: 32px;
    max-width: 720px;
    margin-left: 48px;
    vertical-align: sub;
}

/* 1180 width will be wrapped */

#basicInfo .txtContainer .title {
    position: relative;
    padding: 28px 20px;
    margin-bottom: 20px;
}

#basicInfo .txtContainer .title::after {
    position: absolute;
    content: "";
    background-color: rgb(255, 255, 255);
    bottom: 12px;
    width: 100%;
    height: 3px;
    border-radius: 8px;
}

#basicInfo .txtContainer .title > h1 {
    margin-left: 36px;
}

#basicInfo .txtContainer .title h1:after {
    content: "Fan Voo Twuon Anh";
    position: absolute;
    background-color: #000;
    font-size: 24px;
    top: 0;
    left: 0;
    display: none;
}

#basicInfo .txtContainer .description {
    /* background-color: #a1e7c6; */
    padding: 16px;
    line-height: 24px;
}

#basicInfo .txtContainer .description > p {
    position: relative;
    padding: 12px 16px;
}





#extraInfo {
    width: 60%;
    margin: auto;
    margin-top: 36px;
    min-width: 480px;
    display: flex;
    flex-direction: column;
    /* background-color: crimson; */
}

#extraInfo button {
    margin: auto;
    margin-bottom: 36px;
    font-size: 32px;
    border: 4px solid #235972;
    background-color: #2e5061;
    text-transform: uppercase;
    text-align: center;
    padding: 24px 36px;
    border-radius: 8px;
    cursor: pointer;
    transition: 400ms;
    position: relative;
    overflow: hidden;
    z-index: 5;
}

#extraInfo button::after {
    position: absolute;
    content: "";
    left: 50%;
    top: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    height: 0px;
    width: 0px;
    border-radius: 400px;
    transition: 400ms;
    background-color: azure;
}

#extraInfo button:hover {
    border: 4px solid azure;
    transition: 400ms;
    color: #235972;
}

#extraInfo button:hover:after {
    height: 280px;
    width: 280px;
    color: #235972;
    z-index: -1;
}

#extraInfo button:active {
    transition: 50ms;
    color: azure;
    border: 4px solid rgb(44, 70, 105);
}

#extraInfo button:active:after {
    transition: 50ms;
    background-color: rgb(44, 70, 105);
}

#extraInfo .wrapper {
    /* background-color: brown; */
    width: 520px;
    padding: 16px;
    padding-top: 24px;
    margin: auto;
    transition: 300ms;
    text-align: justify;
}

#extraInfo .wrapper p {
    margin: 12px;
}

#extraInfo .skill {
    font-size: 22px;
    font-weight: 700;
    color: rgb(153, 168, 255);
}

#extraInfo .dbf {
    color: rgb(231, 169, 125);
    font-weight: 700;
    font-size: 22px;
    text-decoration: none;
}

@media screen and (max-width: 1200px)  {
    #Profile {
        padding-top: 120px;
    }

    #basicInfo {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #basicInfo .txtContainer {
        background-color: #00000000;
        width: 75%;
        padding: 32px;
        max-width: 800px;
        vertical-align: sub;
        margin-left: 0px;
    }

    #basicInfo .txtContainer .title {
        position: relative;
        padding: 36px;
        margin-bottom: 20px;
        text-align: center;
    }

    #basicInfo .txtContainer .title h1 {
        margin-left: 0px;
    }

    #basicInfo .txtContainer .title::after {
        left: 0;
    }
    
    #basicInfo .txtContainer .description > p {
        position: relative;
        padding: 12px;
    }
}
