    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(125, 125, 125);
    height: 50px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #fff;
    font-family: 'Nunito';
    scroll-behavior: smooth;
    scroll-padding-top: 60px;
}

body {
    background-color: #2c303d;
}

p {
    font-size: 20px;
}

.Section {
    background-color: rgba(139, 248, 154, 0);
    padding: 36px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.spaces {
    color: #ffffff00;
}


.notYet {
    position: relative;
}

.notYet::after {
    content: "";
    position: absolute;
    top: 40px;
    width: 0px;
    height: 0px;
    left: -40px;
    background-color: black;
}

.notYet:hover:after {
    content: "This feature is unavailable at the moment.";
    width: 200px;
    height: 40px;
    padding: 12px;
    z-index: 10;
}
